:root {
    --text-primary: #000000;
    --background-primary: #FFFFFF;
    --background-button: rgb(240, 240, 240);
    --divider-color: rgb(226, 226, 226);

    --text-green: rgb(5, 163, 87);

    /*breakpoints*/
    --sm: 576px;
    --md: 768px;
    --lg: 992px;
    --xl: 1200px;

    /*font sizes*/
    --text-sm: 0.875rem;
    --section-title-font-size: 2.25rem;

    /*font weight*/
    --text-bold: 700;

    --global-padding: 5%;
}

* {
    box-sizing: border-box;
}

html {
    font-family: 'Roboto', sans-serif;
}

body {
    margin: 0;
    height: 100vh;
    width: 100vw;
    max-width: 1920px;

    background-color: var(--background-primary);

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}